#emp-chat-container #left{
    background: #f8f8f8;
    height: 100%;
    width: 100%;
}#emp-chat-container #right{
    height: 100%;
    width: 100%;
    border-left: 1px solid #ebe8e8;
    border-bottom: 1px solid #ebe8e8;
}
#emp-chat-container{
    height: 680px;
    width: 1300px;
    border: 1px solid #ebe8e8;
    margin: 30px 20px;
}

#emp-chat-container > div{
    height: 100%;
    width: 100%;
}
#header-current-chat-container{
    border-bottom: 1px solid #ebe8e8;
    height: 9%;
}
#header-current-chat-container > div{
    position: relative;
    top: 10px;
    left: 10px;
}
#header-current-chat-container img{
    border-radius: 50%;
    width: 50%;
    display: block;
    margin-left: auto;
    margin-right: auto;
}
.status-icon {
    width: 10px;
    height: 10px;
    border-radius: 50%;
    display: inline-block;
    margin-right: 8px;
}
#chat-status-group{
    width: 100%;
    height: 60px;
    background: #ffffff;
}

#chat-status-group .chat-status-item{
    width: 100%;
    position: relative;
    top: 10px;
    left: 30px;
}

.online {
    background: #06ce16;
}
.offline {
    background: #f50404;
}
.busy {
    background: #cecb06;
}
.meeting {
    background: #1a55f7;
}
.status-name{
    font-size: 13px;
    color: #1d1c1c;
}
#header-current-chat-container .status-name{
    color: #9e9e9e;
    font-style: italic;
}
#chax-box-container{
    width: 100%;
    height: 91%;
}
#messages-list-container{
    width: 100%;
    height: 90%;
}
#message-box-container{

    /* width: 95% !important; */
    height: 10%;
    border: 1px solid #ededed;
    border-radius: 10px;
    margin-bottom: 5px;
}
#message-box-container input[type='text']{
    width: 90%;
    position: relative;
    /* top: 5px; */
    height: 70%;
    outline: none;
    border: none;
}
#message-box-container span{
    float: right;
    position: relative;
    right: 10px;
    top: 20px;
    font-size: 22px;
    color: #5587df;
}