*{
    margin: 0;
    padding: 0;
}

body{
    background: #f6f6f3;
}
header{
    width: 86.6%;
    height: 50px;
    float: right;
    background: #ffffff;
    line-height: 50px !important;
}
header .header-item{
    color: #1e1f1d;
    font-size: 16px;
}
header .header-item:hover{
    color: #a4b88e;
}
#work-func-container{
    float: right;
    margin-right: 30px;
}
header #auth-container {
    padding: 0 20px;
    margin-right: 30px;
    border-left: 1px solid #a39c9c;

}
header #work-func-container .header-item{
    margin-left: 60px;
}

header #auth-container .header-item{
    margin-left: 20px;
}
#left-sidebar{
    background:#ffffff;
    height: 150vh;
    margin-top: -55px;
    color: #000000 !important;
}

#left-sidebar  ul li:active{
    border: 1px solid #62ecc3 !important;
}
#logo-container{
    text-align: center;
    margin: 0 auto;
    /* background: #f3f9f0; */
    padding: 5px 0px;
}
#bh-logo{
    width: 85px;
    height: 85px;
}
#left-menu-container{
    margin-top: 40px;
}
#left-menu-container > div {
    width: 98%;
    float: right;
}

#left-menu-container > div  > ul{
    background: none;
    border: none;
}
.ant-menu-title-content{
    margin-left: 5px !important;
    font-size: 17px;
    font-weight: 500;
    /* color: #ffffff; */
}
.ant-menu-title-content .anticon {
    padding-right: 25px  !important;
}
.ant-menu-submenu ,.ant-menu-item{
    margin-bottom: 35px !important;
}


/* right sidebar */
#right-sidebar  div:last-child > section{
    width: 99%;
    float: right;
    background: #ffffff;
}
#top-breadcrumb-contaner{
    width: 100%;
    padding: 10px 0;
    /* background: #f6f6f6; */
    border-top: 1px solid #ededed;
    border-bottom: 1px solid #ededed;
    display: none;
}
#top-breadcrumb-contaner span {
    margin-left: 10px;
    font-size: 17px;
    color: #303030;
}
#main-side-container{
    height: 100%;
    padding : 10 0;
    position: relative;
    top: 30px;
    width: 100%;
}
#auth-login-container{
    width: 80%;
    height: 400px;
}
#auth-login-container form{
    margin: 0 auto;
    position: relative;
    top: 150px;
}
#user-search-form{
    width: 75%;
    position: relative;
    left: 50px;
    padding: 10px 0;
}
.btn-search{
    width: 130px;
    height: 37px;
    background: #1ca092;
    color: #ffffff;
    margin-top: -7px;
    font-weight: bold;
    border: 1px solid #1ca092 !important;
    /* border: none; */
}
.btn-search:hover{
    background: #ffffff !important;
    color: #1ca092 !important;
}

