.bh-input-form, .bh-input-form-select input{
    width: 220px;
    height: 27px;
}
.search-form-label{
    font-size: 13px !important;
}
.ant-table-wrapper .ant-table-thead >tr>th{
    background: #f6f8f4;
}
.ant-tabs-nav{
    border-bottom: 2px solid #419c98 !important;
}
.ant-tabs-nav-list{
    position: relative;
    left: 50px;
}
.ant-tabs-tab{
    color: #043d40 !important;
    font-size: 13px !important;
    font-weight: 500;
}
.ant-tabs-tab-active{
    background: #419c98 !important;
}
.ant-tabs-tab-active > .ant-tabs-tab-btn{
    color: #ffffff !important;
}
